import React, { useState, useEffect } from "react";
import { Container, Row, Col, Card, Accordion, Button, ButtonGroup, ToggleButton } from "react-bootstrap";
import { LangModule, LangModuleMask } from "./lang_module";

import "./App.scss";

// Compile the language modules
const module_data = require("./lang_modules/japanese.js").module_data;
const modules = module_data.map(json => LangModule.from_json(json));

// Some other helper functions
const mask = (fill: boolean) => Array(modules.length).fill(fill);
const toggle_mask = (mask: Array<boolean>, i: number, value: boolean) => {
    let res = [...mask];
    res[i] = value;
    return res;
};

function App() {
    return (
        <div className="App h-100">
            <Main />
        </div>
    );
}

function Main() {
    let [joined, setJoined] = useState(LangModule.empty());
    let [enabled, setEnabled] = useState(mask(false));
    let [sentences, setSentences] = useState(mask(true));
    let [vocabulary, setVocabulary] = useState(mask(true));

    // Update joined language module when any control changes
    useEffect(() => {
        setJoined(LangModule.join(modules, new LangModuleMask(enabled, sentences, vocabulary)));
    }, [enabled, sentences, vocabulary]);

    return (
        <Container className="h-100">
            <Row id="components" className="h-100">
                <Generator joined={joined}/>
                <ModulesComponent
                    enabled={enabled}
                    sentences={sentences}
                    vocabulary={vocabulary}

                    setEnabled={setEnabled}
                    setSentences={setSentences}
                    setVocabulary={setVocabulary}
                />
            </Row>
        </Container>
    );
}

function Generator(props: any) {
    const joined = props.joined;

    const [generated, setGenerated] = useState("...");

    function generate() {
        setGenerated(joined.random());
    }

    return (
        <Col className="generator">
            <Card>
                <Card.Header>
                    <span className="header-text">Generator</span>
                    <Button
                        onClick={generate}
                    >
                        Generate
                    </Button>
                </Card.Header>
                <Card.Body>
                    <h1>{generated}</h1>
                </Card.Body>
            </Card>
        </Col>
    );
}

function ModulesComponent(props: any) {
    const enabled = props.enabled;
    const sentences = props.sentences;
    const vocabulary = props.vocabulary;

    const setEnabled = props.setEnabled;
    const setSentences = props.setSentences;
    const setVocabulary = props.setVocabulary;

    let module_components = modules.map((lm: LangModule, i: number) => {
        return <ModuleComponent
            key={i}
            name={lm.name}
            // State references
            enabled={enabled[i]}
            sentences={sentences[i]}
            vocabulary={vocabulary[i]}
            // State setters
            setEnabled={(checked: boolean) => setEnabled(toggle_mask(enabled, i, checked))}
            setSentences={(checked: boolean) => setSentences(toggle_mask(sentences, i, checked))}
            setVocabulary={(checked: boolean) => setVocabulary(toggle_mask(vocabulary, i, checked))}
        />;
    });

    const toggleAll = function(checked: boolean) {
        setEnabled(mask(checked));
    }

    return (
        <Col className="h-100">
            <Card className="h-100">
                <Card.Header>
                    <span className="header-text">Modules</span>
                    <Button
                        variant="primary"
                        onClick={() => toggleAll(true)}
                    >
                        Enable All
                    </Button>
                    <Button
                        variant="danger"
                        onClick={() => toggleAll(false)}
                    >
                        Enable None
                    </Button>
                </Card.Header>
                <Card.Body className="h-100">
                    <div id="module-list">{module_components}</div>
                </Card.Body>
            </Card>
        </Col>
    );
}

function ModuleComponent(props: any) {
    const enabled = props.enabled;
    const sentences = props.sentences;
    const vocabulary = props.vocabulary;

    const setEnabled = props.setEnabled;
    const setSentences = props.setSentences;
    const setVocabulary = props.setVocabulary;

    return(
        <Accordion className="module">
            <Card>
                <Card.Body id="module-control">
                    <ButtonGroup
                        toggle className="module-controls"
                    >
                        <ToggleButton
                            type="checkbox"
                            variant="outline-primary"
                            value={0}
                            checked={enabled}
                            onChange={(e) => setEnabled(e.currentTarget.checked)}
                        >
                            {props.name}
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            value={1}
                            checked={sentences}
                            disabled={!enabled}
                            onChange={(e) => setSentences(e.currentTarget.checked)}
                        >
                            Sentences
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            value={2}
                            checked={vocabulary}
                            disabled={!enabled}
                            onChange={(e) => setVocabulary(e.currentTarget.checked)}
                        >
                            Vocabulary
                        </ToggleButton>
                    </ButtonGroup>
                </Card.Body>
            </Card>
            <Card>
                <Accordion.Toggle
                    as={Card.Header}
                    className="module-contents-header small"
                    variant="link"
                    eventKey="0"
                >
                    Contents
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                    <Card.Body>
                        Sentences, Vocabulary
                    </Card.Body>
                </Accordion.Collapse>
            </Card>
        </Accordion>
    );
}

export default App;
